import { NgModule } from '@angular/core';
import { TaskService } from './service/task.service';
import { UsualModule } from './../common/usual/usual.module';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { TaskComponent } from './task.component';
import { TaskRoute } from './task.router';
import { AudioPanelModule } from './../common/audio-panel/audio-panel.module';
import { TaskDetailComponent } from './detail//task-detail.component';
import { TimePipe} from '../common/pipe/time.pipe';
import { NavModule } from './../common/nav/nav.module';
import { TaskIntroComponent } from './intro/task-intro.component';

@NgModule({
  imports: [
    CommonModule,
    AudioPanelModule,
    NavModule,
    UsualModule,
    RouterModule.forChild(TaskRoute)
  ],
  providers: [ TaskService ],
  declarations: [TaskComponent, TaskDetailComponent, TaskIntroComponent]
})
export class TaskModule { }



