import { element } from 'protractor';
import { Audio } from './../../model/audio';
import { TaskService } from './../service/task.service';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { AudioService } from '../../service/audio.service';
import { DatePipe } from '@angular/common';

declare var $: any;

@Component({
  selector: 'app-task-detail',
  templateUrl: './task-detail.component.html',
  styleUrls: ['./task-detail.component.css'],
})
export class TaskDetailComponent implements OnInit {
  private info: any;
  private catalogue: number;
  private courseId = 0;
  constructor( public audioService: AudioService, private activatedRoute: ActivatedRoute, private taskService: TaskService ) { }

  ngOnInit() {
    this.activatedRoute.params.subscribe(res => {
      this.courseId = res.id;
      this.taskService.getCourseDetail(res.id).subscribe( res1 => {
        console.log(res1);
        this.info = res1.task;
        const list = this.ConvertToAudioModel(res1.task.resourceList);
        this.audioService.AddRange(list);
      });
    });
    this.audioService.playEndEvent.subscribe( res => {
      this.taskService.learnRecord(res.Id).subscribe( result => {
      });
    });
  }

  private ConvertToAudioModel(data) {
    const list: Audio[] = [];
    data.forEach(element => {
        const x: Audio = {
        Id: element.id,
        Title: element.fileTitle,
        Cover: element.fileImg,
        Url: element.fileLink,
        Size: 0,
        DuringSecond: ''
      };
      list.push(x);
    });
    return list;
  }

  public playAudio(sn) {
    const right = this.taskService.Right();
    if (right.permission === false) {
      const html = `<a ng-reflect-router-link="/helper" href="/helper/index" style="color:#fff;" >操作失败:你还没有加入班级 <small style="color:#337ab7">点击我,查看帮助</small></a>`;
      $.toptip(html, 'error');
    } else {
      if (right.check === true) {
        if (right.type < this.catalogue) {
          const html = `<a ng-reflect-router-link="/helper" href="/helper/index" style="color:#fff;" >警告:你没有权限学习当前教程 <small style="color:#337ab7">点击我,查看帮助</small></a>`;
          console.log(html);
          $.toptip(html, 'warning');
        }else {
          if (sn != null ) {
            const list = this.audioService.PlayList();
            this.audioService.Toggle(list[sn]);
          }else {
            this.audioService.Toggle();
          }
        }
      } else {
        const html = `<a ng-reflect-router-link="/helper" href="/helper/index" style="color:#fff;" >操作失败:你的权限正在审核 <small style="color:#337ab7">点击我,查看帮助</small></a>`;
        $.toptip(html, 'warning');
      }
    }
  }
}



