  private ConverToOriginalAudioList(x: any): OriginalAudio[] {
    const list: OriginalAudio[] = [];
    x.forEach(element => {
      const audio: OriginalAudio = {
        Cover: element.cover,
        Id: element.id,
        Title: element.title,
        Source: element.source
      };
      list.push(audio);
    });
    return list;
  }
