import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import * as _ from 'lodash';
import { AppConfig } from '../../appconfig/config';

@Injectable()
export class TaskService {

  constructor(private http: Http) { }

  public getCourse(): Observable<any> {
    const data = JSON.parse(localStorage.getItem('appData'));
    const serverUrl = '/app/microlecture/courses';
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this.http.post(serverUrl, {resourceType: AppConfig.resourceType }, options).map((res: Response) => {
      return res.json() ;
    }).catch( (error: any) => Observable.throw(error || 'Server error'));
  }

  public getCourseDetail(id): Observable<any> {
    const data = JSON.parse(localStorage.getItem('appData'));
    const serverUrl = '/app/microlecture/courses/' + id;
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this.http.post(serverUrl, {resourceType: AppConfig.resourceType, openid: data.info.openId }, options).map((res: Response) => {
      return res.json() ;
    }).catch( (error: any) => Observable.throw(error || 'Server error'));
  }

  public Right() {
    const data = this.GetUserInfo();
    return {
      permission: _.isEmpty(data.other.classNo) === true ? false : true,
      classNo: data.other.classNo,
      type: data.other.classType,
      check: data.other.isact === 0 ? false : true
    };
  }
  public GetUserInfo() {
    const data = JSON.parse(localStorage.getItem('appData'));
    return data.info;
  }

  public addComments(courseId1, msg): Observable< any > {
    const member = this.GetUserInfo();
    const data = {
      msg: msg,
      sender: member.wxUserInfo.openId,
      headimg: member.wxUserInfo.headImgUrl,
      nickname: member.wxUserInfo.nickName
    };
    const serverUrl = '/app/microlecture/courses/' + courseId1 + '/comments/add';
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this.http.post(serverUrl, data, options).map((res: Response) => {
      return res.json() ;
    }).catch( (error: any) => Observable.throw(error || 'Server error'));
  }
  public learnRecord(courseid): Observable< any > {
    const member = this.GetUserInfo();
    const data = {
      learner: member.wxUserInfo.openId,
    };
    const serverUrl = '/app/microlecture/learn/record/' + courseid + '/add';
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this.http.post(serverUrl, data, options).map((res: Response) => {
      return res.json() ;
    }).catch( (error: any) => Observable.throw(error || 'Server error'));
  }
}



